import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { logoutAsync, selectUser } from "../../redux/reducers/userReducer";
import {
  NavbarWrapper,
  LogoText,
  HamburgerIcon,
  NavbarButtons,
  NavbarButton,
  NearbyButton,
} from "./NavbarStyle";

function Navbar() {
  const userState = useSelector(selectUser);
  const user = userState.result;
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const dispatch = useDispatch();
  const handleLogout = () => {
    dispatch(logoutAsync());
  };
  return (
    <NavbarWrapper>
      <LogoText to="/home"></LogoText>
      <HamburgerIcon
        $isMenuOpen={isMenuOpen}
        onClick={() => setIsMenuOpen(!isMenuOpen)}
      ></HamburgerIcon>
      <NavbarButtons $isMenuOpen={isMenuOpen}>
        {!user && <NavbarButton to="/register"></NavbarButton>}
        {!user && <NavbarButton to="/login"></NavbarButton>}
        {user && user.data.userLevel === 1 && (
          <NavbarButton to="/add"></NavbarButton>
        )}
        {user && user.data.userLevel === 1 && (
          <NavbarButton to={`/user/${user.data.userId}`}></NavbarButton>
        )}
        {user && user.data.userLevel === 1 && (
          <NavbarButton to={`/backstage/${user.data.userId}`}>
            
          </NavbarButton>
        )}
        {user && user.data.userLevel === 2 && (
          <NavbarButton to={`/admin`}></NavbarButton>
        )}
        {user && (
          <NavbarButton to="/home" onClick={handleLogout}>
            
          </NavbarButton>
        )}
        <NearbyButton to="/nearby"></NearbyButton>
      </NavbarButtons>
    </NavbarWrapper>
  );
}

export default Navbar;
